import {library} from '@fortawesome/fontawesome-svg-core'
import {faChevronDown, faChevronUp, faPaperPlane} from '@fortawesome/free-solid-svg-icons'

library.add(faChevronDown)
library.add(faChevronUp)
library.add(faPaperPlane)

export default {
  layout: 'AdminTemplate',
  name: "online-user",
  data() {
    return {
      windowWidth: process.browser ? window.getComputedStyle(document.documentElement).getPropertyValue('--screen-mode') : 'sm',
      busy: true,
      showOverlay: false,
      data: [],
      searchData: {
        pageIndex: 0,
        rowCount: 0,
        perPage: 10,
        lessonId: null,
        acceptStatus: -1, // 0=unverified , 1= verified , 2= rejected
      },
    }
  },
  mounted() {
    this.loadData()
  },
  methods:
    {

      loadData(index) {
        try {
          this.busy = true
          this.searchData.pageIndex = index
          this.$axios.post("/user/get-online-user", this.searchData).then((res) => {
            this.data = res.data.rows
            this.searchData.rowCount = res.data.count
          }).catch((err) => {

          }).finally(() => {
            this.busy = false
          })

        } catch (e) {
          this.busy = false
        }
      },


    }
}
